import React from 'react';
import { HashLink as Link } from 'react-router-hash-link';

const NavBar = ({ toggle, close }) => {
    return (
        <nav>
            <ul>
                <li><Link smooth to="/#top" onClick={close}></Link></li>
                <li><Link smooth to="/#about" onClick={close}> </Link></li>
                <li><Link smooth to="/#services" onClick={close}></Link></li>
                <li><Link to="/" onClick={toggle}></Link></li>
                <li><Link to="/blog" onClick={close}></Link></li>
                <li><Link smooth to="/#contacts" onClick={close}></Link></li>
            </ul>
        </nav>
    )
}

export default NavBar;
