import React, { useCallback } from 'react';
import { connect } from 'react-redux';
import * as A from '../redux/actions';
import Slider from "react-slick";
import { GiCctvCamera, GiSpy } from 'react-icons/gi';
import { RiAlarmWarningLine } from 'react-icons/ri';
import { IoMdFingerPrint } from 'react-icons/io';
import { FaNetworkWired, FaSignal } from 'react-icons/fa';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './ServicesSlider.css';

const slides = [
    {
        id: 'ss-1',
        icon: <GiCctvCamera />,
        title: '',
        desc: `      Full HD/4K.
                      .
                        .
                   .`
    },
    {
        id: 'ss-2',
        icon: <GiSpy />,
        title: '  ',
        desc: `    .  "",
                ,   ,  ,
                ,  .`
    },
    {
        id: 'ss-3',
        icon: <RiAlarmWarningLine />,
        title: ' ',
        desc: `   . ,    
                .       .
                   .`
    },
    {
        id: 'ss-4',
        icon: <IoMdFingerPrint />,
        title: ' ',
        desc: `        , 
                   .   ?
                 ,  ,  .`
    },
    {
        id: 'ss-5',
        title: '\' ',
        icon: <FaNetworkWired />,
        desc: `      
                .    : ,
                   .`
    },
    {
        id: 'ss-6',
        icon: <FaSignal />,
        title: 'GSM \'',
        desc: ` '     . 
                    
                   ,    
                  3G/4G LTE .`
    }
]

const settings = {
    arrows: false,
    autoplay: true,
    autoplaySpeed: 4000,
    className: 'Slider',
    infinite: true,
    speed: 1000,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
        {
            breakpoint: 1200,
            settings: {
                slidesToShow: 2
            }
        },
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 1
            }
        },
    ]
}

const ServicesSlider = (props) => {
    const { passRef } = props

    const sliderRef = useCallback((el) => passRef(el), [passRef])

    return (
        <Slider ref={sliderRef} {...settings}>
            {slides.map(slide =>
                <div key={slide.id} className="sl-container">
                    <div className="sl-bg">
                        {slide.icon}
                    </div>
                    <div className="sl-text">
                        <h4>{slide.title}</h4>
                        <p>{slide.desc}</p>
                    </div>
                </div>
            )}
        </Slider>
    )
}

const mapDispatchToProps = (dispatch) => {
    return {
        passRef: (ref) => dispatch(A.passRef(ref))
    }
}

export default connect(null, mapDispatchToProps)(ServicesSlider);
